/* eslint-disable @typescript-eslint/no-explicit-any */
import * as React from 'react';
import styled from 'styled-components';
import { reduxForm, Field, InjectedFormProps } from 'redux-form';
import { compose } from 'redux';
import { connect } from 'react-redux';
import * as actions from '../../redux/actions';
import { AuthStateType } from '../../types';

const ModalBackground = styled.div`
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0%;
  left: 0%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
`;

const FormBlock = styled.div`
  width: 250px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;

  @media (min-width: 1024px) {
    width: 500px;
  }
`;

const Form = styled.form`
  border: 2px solid #fc4c4c;
  border-radius: 4px;
  background: #fff;
  padding: 15px 20px;

  @media (min-width: 1024px) {
    padding: 30px 40px;
    border: 3px solid #fc4c4c;
  }
`;

const Fieldset = styled.fieldset`
  border: none;
  background: #fff;
  color: #000;
  margin-top: 10px;
`;

const Label = styled.label`
  display: flex;
  flex-direction: column;
`;

const LinkBlock = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 10px 30px;
`;

const SubmitButton = styled.button`
  font-size: 14px;
  border: none;
  background: #64b171;
  color: #fff;
  padding: 0.5em 0.9em;

  @media (min-width: 1024px) {
    font-size: 16px;
  }
`;

const ErrorMessage = styled.div`
  text-align: center;
  color: #c52626;
`;

interface RegisterFormProps {
  modal: boolean;
  setModal: React.Dispatch<React.SetStateAction<boolean>>;
  signup: any;
  history: any;
  signupErrorMessage: string;
}

const RegisterForm = (
  props: InjectedFormProps<RegisterFormProps> & RegisterFormProps
): React.ReactElement | null => {
  const { handleSubmit } = props;

  const onSubmitSignUp = (formProps: any) => {
    props.signup(formProps, () => {
      props.history.push('/');
    });
  };

  return props.modal ? (
    <>
      <ModalBackground onClick={() => props.setModal(false)} />
      <FormBlock>
        <Form onSubmit={handleSubmit(onSubmitSignUp)}>
          <Fieldset>
            <Label htmlFor="email">
              Email:
              <Field
                id="email"
                name="email"
                type="email"
                component="input"
                autoComplete="none"
              />
            </Label>
          </Fieldset>
          <Fieldset>
            <Label htmlFor="password">
              Password:
              <Field
                id="password"
                name="password"
                type="password"
                component="input"
                autoComplete="none"
              />
            </Label>
          </Fieldset>
          <ErrorMessage>{props.signupErrorMessage}</ErrorMessage>
          <LinkBlock>
            <SubmitButton>Sign Up</SubmitButton>
          </LinkBlock>
        </Form>
      </FormBlock>
    </>
  ) : null;
};

function mapStateToProps(state: AuthStateType): {} {
  return { signupErrorMessage: state.auth.signupErrorMessage };
}

export default compose<any>(
  connect(mapStateToProps, actions),
  reduxForm({ form: 'signup' })
)(RegisterForm);
