/* eslint-disable prefer-destructuring */
/* eslint-disable no-case-declarations */
/* eslint-disable import/no-anonymous-default-export */
/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  ADD_ITEM,
  REMOVE_ITEM,
  DELETE_ITEM,
  CLEAR_CART,
} from '../actions/types';

const initialState = {
  items: [],
  totalPriceUSD: 0,
  totalPriceEURO: 0,
  deliveryFee: {
    USD: 4,
    EUR: 3,
  },
};

function findUniqueItem(state: any, id: string) {
  return state.items.find((el: any) => el.id === id);
}

function findAndDeleteItem(state: any, id: string) {
  const index = state.items
    .map((el: any) => {
      return el.id;
    })
    .indexOf(id);
  state.items.splice(index, 1);
  return state.items;
}

export default function (state: any = initialState, action: any) {
  switch (action.type) {
    case ADD_ITEM:
      const item = action.payload.item[0];
      const currency = action.payload.currency;
      const uniqueItem = findUniqueItem(state, item.id);
      if (uniqueItem === undefined) {
        item.quantity = 1;
        if (currency === 'USD') {
          const newTotalUsdPrice = state.totalPriceUSD + item.price.USD;
          const newTotalEuroPrice = state.totalPriceEURO + item.price.EUR;
          const newItems = [...state.items, ...action.payload.item];
          const newState = {
            ...state,
            items: newItems,
            totalPriceUSD: newTotalUsdPrice + state.deliveryFee.USD,
            totalPriceEURO: newTotalEuroPrice + state.deliveryFee.EUR,
          };
          return newState;
        }
        if (currency === 'EURO') {
          const newTotalUsdPrice = state.totalPriceUSD + item.price.USD;
          const newTotalEuroPrice = state.totalPriceEURO + item.price.EUR;
          const newItems = [...state.items, ...action.payload.item];
          const newState = {
            ...state,
            items: newItems,
            totalPriceUSD: newTotalUsdPrice,
            totalPriceEURO: newTotalEuroPrice,
          };
          return newState;
        }
      }
      if (uniqueItem !== undefined) {
        item.quantity += 1;
        const newTotalUsdPrice = state.totalPriceUSD + item.price.USD;
        const newTotalEuroPrice = state.totalPriceUSD + item.price.EUR;
        const newState = {
          ...state,
          totalPriceUSD: newTotalUsdPrice,
          totalPriceEURO: newTotalEuroPrice,
        };
        return newState;
      }
      return state;
    case REMOVE_ITEM:
      const cartItem = action.payload[0];
      if (cartItem.quantity === 1) {
        cartItem.quantity = 0;
        const newTotalPrice = state.totalPriceUSD - cartItem.price.USD;
        const newItemList = findAndDeleteItem(state, cartItem.id);
        const newState = {
          ...state,
          items: newItemList,
          totalPriceUSD: newTotalPrice,
        };
        return newState;
      }
      if (cartItem.quantity > 1) {
        cartItem.quantity -= 1;
        const newTotalPrice = state.totalPriceUSD - cartItem.price.USD;
        const newState = { ...state, totalPriceUSD: newTotalPrice };
        return newState;
      }
      return state;
    case DELETE_ITEM:
      const cartItemDelete = action.payload[0];
      const newItemList = findAndDeleteItem(state, cartItemDelete.id);
      const newState = {
        ...state,
        items: newItemList,
      };
      return newState;
    case CLEAR_CART:
      return initialState;
    default: {
      return state;
    }
  }
}
