import * as React from 'react';
import styled from 'styled-components';
import CartIcon from '../../assets/cart-icon.svg';

const CartImage = styled.img`
  width: 30px;
  height: 30px;

  margin-bottom: 5px;
`;

const Cart = (props: any) => {
  return (
    <CartImage
      src={CartIcon}
      alt=""
      onClick={() => props.setBusket(!props.busket)}
    />
  );
};

export { Cart };
