/* eslint-disable @typescript-eslint/no-explicit-any */
import * as React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import * as actions from '../../redux/actions';
import { PizzaCard } from './PizzaCard';

const Container = styled.div`
  max-width: 1348px;
  margin: 0 auto;
`;

const PizzaGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 250px));
  justify-content: center;
  grid-gap: 40px;
  padding: 30px 0;

  @media (min-width: 1024px) {
    grid-gap: 75px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 330px));
    justify-content: center;
  }
`;

interface DataProps {
  pizzas: [
    id: string,
    name: string,
    price: {
      USD: number;
      EUR: number;
    },
    description: string,
    image: string
  ];
  fetchPizzas: any;
}

const PizzaList = (props: DataProps): JSX.Element => {
  React.useEffect(() => {
    props.fetchPizzas('http://localhost:3090/pizzas');
  }, []);

  return (
    <Container>
      <PizzaGrid>
        {props.pizzas &&
          props.pizzas.map((el: any) => {
            return (
              <PizzaCard
                key={el.id}
                id={el.id}
                name={el.name}
                price={el.price}
                description={el.description}
                image={el.image}
              />
            );
          })}
      </PizzaGrid>
    </Container>
  );
};

function mapStateToProps(state: any) {
  return { pizzas: state.pizzas };
}

const connected = connect(mapStateToProps, actions)(PizzaList);

export { connected as PizzaList };
